.product_card {
  box-shadow: 10px 5px 5px 12px lightblue	;
    width: 400px;
    margin: 30px 60px;
    text-align: center;
    font-family: arial;
    height: 100%;
    padding-bottom: 25px;
    padding-top: 15px;
    border-radius: 2em;
  } 


  .product_card_related {
    box-shadow: 10px 5px 5px 12px lightblue	;
      width: 250px;
      margin: 30px 30px;
      text-align: center;
      font-family: arial;
      height: 80%;
      padding-bottom: 25px;
      padding-top: 15px;
      border-radius: 2em;
    } 

    .product_card_related h2{
      white-space: nowrap;
      overflow: hidden;
      padding: 1em;
    }


  .product_card_img{
    border-radius: 1em;
  }

  .product_card_img_related{
    border-radius: 1em;
    height:70%;
    width:190px;
  }
  
  .price {
    color: #015265;
    font-size: 35px;
    font-weight: 900;
  }

  .product_card img {
  width: 100%;
    height: 250px;
    display: block;
    object-fit: cover;
  }

  .product_card_related img{
    width: 100%;
    height: 250px;
    display: block;
    object-fit: cover;
  }

.product_card .product-title{
  text-transform: uppercase;
  margin-top: 1em;
  margin-bottom:.5em;
}

.product_card_related .product-title{
  text-transform: uppercase;
  margin-top: .5em;
  margin-bottom:.5em;
}
  .product_card .add-to-cart{
      color: white; 
      background-color: 	#7d9027;
      text-align: center;
      margin: 10px auto;
      display:block;
      width: 40%;
      overflow:auto;  
      padding: 6px 3px;
  }

  .product_card_related .add-to-cart{
    color: white; 
    background-color: 	#7d9027;
    text-align: center;
    margin: 10px auto;
    display:block;
    width: 40%;
    overflow:auto;  
    padding: 6px 3px;
  }
  .product_card .view-more{
    text-decoration: underline;
  }

  .product_card_related .view-more{
    text-decoration: underline;
  }
  
  .product_card button:hover {
    opacity: 0.7;
  }